import React from 'react';
import {
    Popover,
    Button,
    Icon,
    Form,
    Tooltip,
} from 'antd';
const FormItem = Form.Item;
import filterTableStyle from '../../css/plugin/filterTable/filterTable';


class FilterTableFrom extends React.Component {

    constructor(props) {
        super(props);
    }

    getChildren() {
        const {
            children,
            form,
        } = this.props;
        const checkConfirm = this.checkConfirm;
        const { getFieldDecorator } = this.props.form;
        let node = [];
        
        React.Children.map(children, function (child, i) {
            const {
                key,
                props,
            } = child;
            const {
                vaildType,
                value,
                defaultValue,
                errmsg,
             } = props;
             
            if (vaildType) {
                switch (vaildType) {
                    case 'input':
                        node.push(
                            <FormItem>{
                                getFieldDecorator(key, {
                                    rules: [
                                        {
                                            required: true, message: 'Please input your input!',
                                        }
                                    ],
                                })(
                                    child
                                )}
                            </FormItem>
                        );
                        break;
                    case 'select':

                        node.push(
                            <FormItem>{
                                getFieldDecorator(key, {
                                    rules: [
                                        {
                                            required: true, message: errmsg,
                                        }
                                    ],
                                    initialValue: defaultValue
                                })(
                                    child
                                )}
                            </FormItem>
                        );
                        break;
                    default:
                        throw new Error('no type in vaildType')
                }
            } else {
                node.push(
                    <span>{child}</span>
                )

            }

        });
        return node;
    }
    render() {
        const children = this.getChildren();
        return (
            <Form>
                {children}
            </Form>
        )
    }
}
const FilterTableFromContent = Form.create()(FilterTableFrom);


class FilterTable extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            controlVisible: false,
        }
    }
    controlVisible() {

        const {
            tabDisabled,
        } = this.props;
        this.setState({
            controlVisible: !this.state.controlVisible,
        });

        if(tabDisabled){
            if(!this.state.controlVisible){
                document.querySelector('.ant-tabs-tab-active').setAttribute("class", "ant-tabs-tab ant-tabs-tab-active ant-tabs-tab-disabled");
                let arr = document.querySelectorAll('.ant-tabs-tab:not(.ant-tabs-tab-active)');
                for(let item of arr) {
                    item.setAttribute("class", "ant-tabs-tab ant-tabs-tab-disabled");
                }
            }else{
                document.querySelector('.ant-tabs-tab-active').setAttribute("class", "ant-tabs-tab ant-tabs-tab-active");
                let arr = document.querySelectorAll('.ant-tabs-tab:not(.ant-tabs-tab-active)');
                for(let item of arr) {
                    item.setAttribute("class", "ant-tabs-tab");
                }
            }
        }
        
    }
    
    ok() {
        const {
            reg,
            ok,
            children,
        } = this.props;


        /**
         * 验证
         */
        let result = false;
        this.FilterTableFromContent.validateFields((err, values) => {
            
            if (!err) {
                console.log('Received values of form: ', values);
                result = true;
            }
        });
        if (!result) return;


        if (ok) {
            
            ok();
            
            this.controlVisible();
            return;
        }

        this.controlVisible();

    }

    cancel() {

        const {
            cancel,
        } =this.props;

        if(cancel){
            cancel();
            this.controlVisible();
            return;
        }
        this.controlVisible();
    }

    getPopupContainer(triggerNode) {
        
        return triggerNode.parentNode;
    }

    contentProps() {
        const {
            children,
            hasBtn = true,
            btnStyle = {},
        } = this.props;
        return (
            <div style={{minWidth: 260}}>
                <FilterTableFromContent ref={node => this.FilterTableFromContent = node}>{children}</FilterTableFromContent>
                {
                    hasBtn?
                    <div className="btn_div" style={btnStyle}>
                        <Button
                            className="ok"
                            type="primary"
                            onClick={e => this.ok(e)}
                        >
                            保存
                        </Button>
                        <Button
                            className="cancel"
                            onClick={e => this.cancel(e)}
                        >
                            取消
                        </Button>
                    </div>
                    :
                    ''
                }
            </div>
        )
    }
    render() {
        const {
            title,
            placement,
            type,
            dom,
            overlayStyle,
            overlayClassName,
            fixed,
        } = this.props;
        
        let popoverProps = {
            content: this.contentProps(),
            trigger: "click",
            visible: this.state.controlVisible,
            placement: placement ? placement : 'bottom',
            overlayStyle: overlayStyle? overlayStyle:{},
            overlayClassName: overlayClassName? overlayClassName:'',
            arrowPointAtCenter: true,
        }

        if(fixed){
            popoverProps.getPopupContainer = (triggerNode) => this.getPopupContainer(triggerNode);
        }

        return (
            <div style={{position: 'relative'}}>
                {title}

                <Popover
                    {...popoverProps}
                >
                    <Icon type={type ? type : ''} onClick={() => this.controlVisible()}>
                        {dom ? dom : ''}
                    </Icon>
                </Popover>

            </div>
        );
    }
}


export default FilterTable;


